import React from 'react';
//import API from '../routes/API';

const projects = [];

export class Project extends React.Component{

    render()    {
        return (
            <div>
                {projects.map((project) =>  {
                    <div className="row">
                        <div className="col s12 m12 l12 xl4 left blue-grey darken-2">
                            <h4 className="card-title blue-grey darken-2 teal-text text-lighten-5 center-align">{project.title}</h4>
                            <div className="card blue-grey white-text">
                                <div class="card-image">
                                    <img src={project.image} alt={project.title}/>
                                </div>
                                <div className="card-content">
                                    <p>{project.description}</p>
                                </div>
                                <div className="card-action">
                                    <a className="teal-text text-lighten-3"  href={project.deployed} target="_blank">View Deployed Site</a>
                                    <a className="teal-text text-lighten-3"  href={project.repo} target="_blank">View Repo</a>
                                </div>
                            </div>
                        </div>
                    </div>
                })};
            </div>
            
        )
    }
}